
*,
*:before,
*:after {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-text-stroke: .5px grey;
    font-color: black;
}

.AddpokeBackground {
    width: 430px;
    height: 700px;
    position: absolute;
    transform: translate(-50%, -50%);
    left: 50%;
    top: 66%;
    background-color: #080710;
    border-radius: 2%;
}

.AddpokeBackground .shape {
    box-sizing: border-box;
      border: 10px solid #262122;
    height: 250px;
    width: 250px;
    position: absolute;
    border-radius: 100%;
    margin: 0 auto;

}

.AddpokeBackground .shape:before,
.AddpokeBackground .shape:after {
  content: " ";
  border-radius: 100%;
  display: block;
  position: absolute;
}

.AddpokeBackground .shape:before {
  background: #262122;
  width: 80px;
  height: 80px;
  margin-top: 75px;
  margin-left: 75px;
}

.AddpokeBackground .shape:after {
  background: #fff;
  width: 80px;
  height: 80px;
  margin-top: 75px;
  margin-left: 75px;
  border: double 20px #262122;
}

.shape:first-child {
    background: linear-gradient( 150deg, #BA0C2F 0%, #BA0C2F 48%, #262122 48%, #262122 52%, #FFF 52%, #FFF 100%);
    left: -140px;
    top: -80px;
}

.shape:last-child {
    background: linear-gradient( 210deg, #BA0C2F 0%, #BA0C2F 48%, #262122 48%, #262122 52%, #FFF 52%, #FFF 100%);
    right: -140px;
    bottom: -80px;
}

.addpokeForm {
    height: 675px;
    width: 400px;
    background-color: rgba(255, 255, 255, 0.13);
    position: absolute;
    transform: translate(-50%, -50%);
    top: 66%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 2px solid rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 40px rgba(8, 7, 16, 0.6);
    padding: 50px 35px;
}

.addpokeForm * {
    font-family: 'Poppins', sans-serif;
    color: #ffffff;
    letter-spacing: 0.5px;
    outline: none;
    border: none;
}

.addpokeForm h3 {
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}

.addpokeForm label {
    display: block;
    margin-top: 20px;
    font-size: 16px;
    font-weight: 500;
}
.addpokeForm input {
  text-align: center;
    display: block;
    height: 50px;
    width: 100%;
    position: relative;
    background-color: rgba(255, 255, 255, 0.07);
    border-radius: 3px;
    padding:  10px;
    margin-top: 8px;
    font-size: 18px;
    font-weight: 600;
}

::placeholder {
    color: #e5e5e5;
}

.addpokeForm button {
    margin-top: 25px;
    width: 100%;
    background-color: #ffffff;
    color: #080710;
    padding: 15px 0;
    font-size: 18px;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
}

.addColorToFont {
      color:#09080e;
      font-size: small;
      font-weight: 300;
}

.social {
    margin-top: 30px;
    display: flex;
}

.social div {
    background: red;
    width: 150px;
    border-radius: 3px;
    padding: 5px 10px 10px 5px;
    background-color: rgba(255, 255, 255, 0.27);
    color: #eaf0fb;
    text-align: center;
}

.social div:hover {
    background-color: rgba(255, 255, 255, 0.47);
}

.social .fb {
    margin-left: 25px;
}

.social i {
    margin-right: 4px;
}

